body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: rgb(40,44,51) !important;
    color: whitesmoke !important;
}

.App {
    display: flex;
    align-items: center;
    height: 100vh;

}

.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    padding: 20px 0;
    width: 500px;
    max-width: 100%;
}


@media screen and (max-width: 768px) {
    .main {
        width: 100vw;
    }
    .header {
        font-size: 1rem;
        padding: 25px !important;
    }
    .header::after {
        width: 80vw !important;
    }
    .header 
    h1 {
        font-size: 1.9rem;
        text-align: left;
    }
    .header 
    .header-right {
        text-align: right;
        width: 100%;
    }

    .header 
    .header-right 
    img {
        width: 100px !important;
        height: 100px !important;
    }

}

@media screen and (max-width: 260px) {

    .header 
    .header-right 
    img {
        width: 60px !important;
        height: 60px !important;
    }
}


.header {
    display: flex;
    width: 100%;
    align-items: flex-end;
    justify-content: space-between;
    padding: 15px;
    position: relative;
    margin-bottom: 20px;
}


.header 
.header-left,
.header 
.header-right {
    display: flex;
    flex-direction: column;
}

.header 
.header-left {
    align-items: flex-start;
}

.header 
.header-left 
.date {
    text-align: left;
}

.header 
.header-right {
    align-items: flex-end;
}

.header 
.header-right 
img {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0px;
    right: 10px;
}

.header::after {
    content: "";
    margin: 0 auto;
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 80%;
    border-bottom: 0.5px solid white;
}


/* SUNRISE */

.sun-position {
    display: flex;
    align-items: center;
    font-size: 0.8rem;
}

.sunrise, 
.sunset {
    position: relative;
}

.sunrise::after,
.sunset::after{
    display: inline-block;
    position: absolute;
    top: 40px;
    left: 0;
    content: '';
    border: 10px solid #212529;
    z-index: 2;
    width: 100%;
}

.sunrise 
.sunrise-sun{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-animation: sunrise 3s ease-in-out;
            animation: sunrise 3s ease-in-out;
}
.sunset 
.sunset-moon {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    -webkit-animation: sunset 5s ease-in-out;
            animation: sunset 5s ease-in-out;
    position: absolute;
}
.sunset 
.sunset-sun {
    -webkit-transform: translateY(20px) scale(0.3);
            transform: translateY(20px) scale(0.3);
    animation: sunset-sun 3s reverse;
}

@-webkit-keyframes sunrise {
    from {
        -webkit-transform: translateY(20px) scale(0.3);
                transform: translateY(20px) scale(0.3);
    }

    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes sunrise {
    from {
        -webkit-transform: translateY(20px) scale(0.3);
                transform: translateY(20px) scale(0.3);
    }

    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}
@-webkit-keyframes sunset {
    from {
        -webkit-transform: translateY(20px) scale(0.3);
                transform: translateY(20px) scale(0.3);
    }

    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}
@keyframes sunset {
    from {
        -webkit-transform: translateY(20px) scale(0.3);
                transform: translateY(20px) scale(0.3);
    }

    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}
@-webkit-keyframes sunset-sun {
    from {
        -webkit-transform: translateY(20px) scale(0.3);
                transform: translateY(20px) scale(0.3);
    }

    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}
@keyframes sunset-sun {
    from {
        -webkit-transform: translateY(20px) scale(0.3);
                transform: translateY(20px) scale(0.3);
    }

    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}


